<template class="body">
  <div @mousemove="mousemove" style="margin: auto 15%">
    <div class="floating" :style="cardStyle">
      <div class="depth"></div>
      <div class="depth"></div>
      <div class="depth"></div>
      <div class="card-body">
        <v-img
          class="logo"
          :style="svgStyle"
          src="https://res.cloudinary.com/riichi-id/image/upload/v1681113899/riichi.id/web/card_logo_tuhhm4.png"
        />
        <div class="points">{{ $n(memberdata.points) }} P</div>
        <div class="m-id">{{ memberdata.mid }}</div>
        <div class="name" :style="textStyle">{{ memberdata.name }}</div>
        <div class="region" :style="textStyle">JAKARTA RAYA</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    console.log(this.memberdata)
    return {
      cardStyle: null,
      svgStyle: null,
      textStyle: null
    }
  },
  props: [
    'memberdata'
  ],
  methods: {
    mousemove(event) {
      let card_x = this.getTransformValue(event.clientX,window.innerWidth,56);
      let card_y = this.getTransformValue(event.clientY,window.innerHeight,56);
      let shadow_x = this.getTransformValue(event.clientX,window.innerWidth,20);
      let shadow_y = this.getTransformValue(event.clientY,window.innerHeight,20);
      let text_shadow_x = this.getTransformValue(event.clientX,window.innerWidth,28);
      let text_shadow_y = this.getTransformValue(event.clientY,window.innerHeight,28);
      this.cardStyle = `transform: rotateX(${card_y/1}deg) rotateY(${+card_x}deg); box-shadow: rgba(0, 0, 0, 0.55) ${-card_x}px ${+card_y/1}px 55px;`
      this.svgStyle = `filter: drop-shadow(${-shadow_x}px ${shadow_y/1} 8px rgba(0, 0, 0, .6));`
      this.textStyle = `text-shadow: ${-text_shadow_x}px ${text_shadow_y/1}px 6px rgba(0, 0, 0, .8);`
    },
    getTransformValue(v1, v2, value) {
      return ((v1 / v2 * value - value / 2) * 1).toFixed(1);
    }
  }
}
</script>

