<template>
  <v-container fluid>
    <v-layout v-if="!isLoading" align-center justify-center style="margin-top: 5%">
      <v-membercard :memberdata="m" style="height: 100%"></v-membercard>
    </v-layout>
    <v-layout v-if="isLoading" align-center justify-center>
      <loading-progress
        :indeterminate=true
        size="300"
        shape="line"
        fillDuration="2"
      />
    </v-layout>
  </v-container>
</template>

<script>
import VirtualCard from '@/components/member/VirtualCard.vue';
export default {
  name: 'MembersView',
  components: {
    'v-membercard': VirtualCard
  },
  props: {
    memberId: String
  },
  data() {
    return {
      isLoading: true,
      m: {}
    }
  },
  async created() {
    if (this.memberId) {
      const resp = await fetch(`https://script.google.com/macros/s/AKfycbxeXQIG2EGPfv7LWe9e4o1X1shodG4QTkHn3781xR-NUxqmkWEUqYMEiXHII7r2xXee/exec?memberId=${this.memberId}`);
      const data = await resp.json();
      this.m = {
        name: data.data.name,
        points: data.data.points,
        mid: data.data.mid
      };
      this.isLoading = false;
    }
  }
};
</script>
